				</view>
			</view>
		</view>
		<view class="details_note">
			<view style="width: 100%;">
				<view class="details_table" style="margin-bottom: 18rpx;">
					<view>商品名称</view>
					<view>商品数量</view>
					<view>商品单价</view>
					<view style="width: 30%;">商品总价</view>
				</view>
				<view class="details_table details_table_list" v-for="(item,index) in form.goods_data" :key="index">
