		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				id:'',
				form:{},
			}
		},
		onLoad(option) {
			console.log('option',option)
			if(option.data) {
				this.id = JSON.parse(decodeURIComponent(option.data))
				this.listInterface()
			}
			
		},
		methods: {
			// 接口列表
			listInterface() {
				this.http({
				    url: this.api.order_report_info,
				    method: "POST",
				    data: {
				        uid: this.common.get("uid"),
				        id:this.id,
				    },
				    success: res => {
						this.form = res
				    }
				})
			},
		}
	}
</script>

